// fade transition
.fade-enter-active,
.fade-leave-active {
  transition: all 300ms;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
  transform: translateY(10px);
}

// modal transition
.modal-enter-active,
.modal-leave-active {
  opacity: 0;
}

.modal-enter-active .modal-content {
  transform: scale(0.75);
}

.modal-leave-active .modal-content {
  transform: scale(0.75);
}
